import {Component, OnInit, ViewChild, ElementRef, OnChanges, style} from '@angular/core';
import {Task} from './task';
import {TaskService} from '../task.service';
import {MatSnackBar,MatSnackBarConfig,MatSnackBarHorizontalPosition,MatSnackBarVerticalPosition} from '@angular/material';

@Component({
    selector: 'task-list',
    templateUrl: './task.component.html',
    styleUrls: ['./task.component.css']
})
export class TaskComponent implements OnInit{
    public tasks:Task[];
    public edited:boolean=false;
    public deleted:any;
    public emptyValidationFlag:boolean=false;
    public editedIndex:number = -1;
    public nextId:number=0;
    public loader:boolean=false;
    public snackColor:String=null;
    constructor(private taskService: TaskService,public snackBar:MatSnackBar){}
    ngOnInit(){
       this.loader=true;
       this.getTasks();
    }

    private getTasks():void{
     
        this.taskService.getTasks().subscribe(
            taskRx => {this.tasks = taskRx,
            this.nextId = this.tasks[this.tasks.length-1].id,
            this.loader=false;}, //Bind to view
            err => {
                // Log errors if any
                console.log(err);
            });
    }
   
    private emptyValidation(newTaskName: String){
        this.emptyValidationFlag = false;
        if(newTaskName != '' && newTaskName !='/^\S*$/'){
            this.emptyValidationFlag = true;
        }
    }

    private createTask(newTaskName: String){
        newTaskName = newTaskName.trim();
        this.emptyValidation(newTaskName);
        if(this.emptyValidationFlag == true){
            this.loader=true;
            if(this.tasks.length == 0){
                this.nextId = 0;
            }
            this.nextId++;
            this.taskService.createTask(new Task(this.nextId,newTaskName)).subscribe(taskRX => {
                this.tasks.push(taskRX),
                this.loader=false;
                this.snackColor='add';
                this.showMessage();
            },
                err => {
                    console.log(err);
                });
                this.resetForm();
        }
        else{
            this.snackColor='validation';
            this.showMessage();
        }
    }

    @ViewChild('newTaskEl') newTaskEl:ElementRef;

    private editTask(index:number, editedTask:Task){
        this.newTaskEl.nativeElement.value= editedTask.taskName;
        this.edited = true;
        this.editedIndex = index;
    }

    private updateTask(updatedTask:String){
        this.loader=true;
        this.taskService.updateTask(new Task(this.tasks[this.editedIndex].id,updatedTask))
        .subscribe(taskRxUpdated => {
            this.tasks[this.editedIndex].id = taskRxUpdated.id,
            this.tasks[this.editedIndex].taskName = taskRxUpdated.taskName,
            this.loader=false;
            this.snackColor='update';
            this.showMessage();
        },
        err => {
            console.log(err);
        });
        this.resetForm();
        this.edited = false;
    }

    private cancelUpdateTask(){
        this.resetForm();
        this.edited = false;
    }

    private deleteTask(index:number, taskToDelete:Task){
        if(confirm("Are you sure to delete Task : "+taskToDelete.id+". "+taskToDelete.taskName)) {
            this.loader=true;
            this.taskService.deleteTask(taskToDelete.id).subscribe(taskRx => {
                this.tasks = taskRx
                this.loader=false;
                this.snackColor='delete';
                this.showMessage();
            },
           err => {
                console.log(err)
            });
        }
    }
    @ViewChild('formRef') form:ElementRef;

    public resetForm(){
        this.form.nativeElement.reset();
    }

    public showMessage():void{
        let config = new MatSnackBarConfig();
        config.horizontalPosition = 'center';
        config.verticalPosition = 'top';
        config.extraClasses = this.snackColor=='validation'? ['validationSnack']:(this.snackColor == 'add'?
        ['addSnack']:(this.snackColor == 'update'? ['updateSnack']:['deleteSnack']));
        config.duration = 3000;
        this.snackBar.open(
            this.snackColor == 'validation'?"No need to create Empty Task":(this.snackColor == 'add'?
            "Task Saved successfully":(this.snackColor == 'update'? "Task Updated Successfully":"Task Deleted Successfully")),"OK", config);
    }
}


